<template>
	<v-container fluid class="pa-2">
        <v-card>
            <v-card-text class="text-xs-left markdown-body" v-html="html(content)"></v-card-text>
        </v-card>
    </v-container>
</template>
<script>
import { mapActions } from 'vuex'
import marked from 'marked'
import "github-markdown-css/github-markdown.css"
export default {
	name:'help',
	data(){
		return {
			content:'## \n- \n- "Todo"App\n- Markdown\n\n## \n1. \n2. \n3. \n4. \n',
		}
	},
	methods:{
		...mapActions('appShell/appHeader', [
            'setAppHeader'
        ]),
        ...mapActions('appShell/appBottomNavigator', [
            'hideBottomNav'
        ]),
        html(text){
        	return marked(text)
        }
	},
	created:function() {
		this.setAppHeader({
			title: '',
            show: true,
            showMenu: false,
            showBack: true,
            actions: []
		});
		this.hideBottomNav();
	}
}
</script>



